.techstack 
{
    padding-left: 130px;
    margin-top: 60px;
}
.techstack hr {
    color: #138781;
    width: 400px;
    margin: 10px auto;
    
}
.techstack h2 {
    font-weight: bold;
    color: #138781;
}
.techstack .card {
    cursor: pointer;
}
.techstack .tech-icon{
    font-size: 2rem;
    margin-right: 10px;
}
.techstack .card:hover {
 background-color: #138781;
 color: white;
 border: none;
 transition: 1s;
 box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

/* MEDIA Query */
@media(max-width:600px) {
    .techstack {
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }
   
    .techstack 
        hr {
        width: 280px;
        
    }
}