.project{
    margin: 50px 0px;
    margin-left: 140px;
}
.project hr {
    color: #138781;
    width: 400px;
    margin: 10px auto;
    
}
.project h2 {
    font-weight: bold;
    color: #138781;
}
#ads {
    margin-top: 30px 0px 30px 0px;
}
#ads .card-notify-badge{
 position: absolute;
 left: -10px;
 top: -20px;
 background-color: #f29f67;
 text-align: center;
 border-radius: 30px 30px 30px 30px;
 color: black;
 padding: 5px 10px;
 font-size: 14px;
}
.card-image img{
    height: 150px;
    width: 100%;
}
.card-detail-badge {
    background-color: #e0b50f;
    text-align: center;
    border-radius: 30px 30px 30px 30px;
    color: black;
    padding: 5px 10px;
    font-size: 14px;
}
.card-image-overlay span {
    display: inline-block;
    margin-left: 5px;
} 
.project .card:hover {
    transition: all 1s ease;
    box-shadow: rgba(46, 61, 73, 0.15) 12px 15px 20px 0px;
    border-radius: 4px;
    background-color: white;
}
    .ad-btn {
    text-transform: uppercase;
    width: 150px;
    background-color: #138781;
    color: white;
    border-radius: 80px;
    font-size: 16px;
    line-height: 35px;
    text-decoration: none;
    margin: 20px auto 20px auto;
    overflow: hidden;
    position: relative;
    display: block;
    text-align: center;
    height: 40px;
}
.ad-btn:hover {
    background-color: white;
    color: #138781;
    background: transparent;
    border: 2px solid #138781;
    transition: all 1s ease;
    box-shadow: rgba(46, 61, 73, 0.15) 12px 15px 20px 0px;
}

/* MEDIA Query */
@media(max-width:600px) {
    .project {
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }
   
    .project 
        hr {
        width: 280px;
        
    }
    .project .card {
        margin-top: 30px;
    }
}